<template>
    <a class="base-social-button" target="_blank" :class="socialButtonClass">
    </a>
</template>
<script>
export default {
    data() {
        return {
            xss: '20px',
        }
    },
    name: 'base-social-button',
    props: {
        background: {
            type: Boolean,
            default: false
        },
        icon: {
            type: String,
            default: ''
        }
    },
    computed: {
        socialButtonClass() {
            return {
                'background': this.background,
            }
        },

    }
}

</script>
<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.instagram {
    @include instagramIc;
}

.facebook {
    @include facebookIc;
}

.youtube {
    @include youtubeIc;
}

.mail{
    @include mailIc;
}

.base-social-button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color2;
    color: $color3;
    border-radius: 2px;
    width: 30px;
    height: 30px;

    &:hover {
        background-color: $color4;

    }

    &:not(.background) {
        background-color: $color3;
        color: $color2;

        &:hover {
            color: $color4;
        }
    }

}
</style>