<template>
    <accordion-component title="Year">
        <template #body1>
            <div class="price-block">
                <div class="info-price-block">
                    <p class="year">{{ rangeSlider.value[0] }}</p>
                    <p class="year">{{ rangeSlider.value[1] }}</p>
                </div>

                <Slider class="slider-range" v-model="rangeSlider.value" :min="min" :max="max" @input="this.rangeSlider.checked = true"
                    :tooltips="false" v-bind="rangeSlider" @change="updateFilter"></Slider>
            </div>
        </template>
        <template #tabs v-if="rangeSlider.checked == true">
                <div class="tabs-item" v-if="this.rangeSlider.checked != false" @click="DelSelectYear" >{{ this.rangeSlider.value[0] }} - {{ this.rangeSlider.value[1] }}</div>
        </template>
    </accordion-component>
</template>
<script>
import Slider from '@vueform/slider'
import accordionComponent from '@/components/inventory-page/accordion-component.vue'
export default {
    components: { accordionComponent, Slider },
    inject: ['year', 'SetYear'],
    data() {
        return {
            min: 1980,
            max: 2023,
            rangeSlider: { id: 1, title: 'Year', value: [1980, 2023], checked: false },
        }
    },
    methods:{
        updateFilter() {
                this.SetYear(this.rangeSlider);
        },
        DelSelectYear(){
            this.rangeSlider.value = [this.min,this.max];
            this.rangeSlider.checked = false;
        }
    }
}
</script>
<style  scoped lang="scss">
@import "@/assets/scss/default/default.scss";
@import '@vueform/slider/themes/default.css';

.slider-range {
    margin-bottom: 10px;
    cursor: pointer;
    --slider-bg: #D7D7D7;
    --slider-connect-bg: #7481FF;
    --slider-connect-bg-disabled: #7481FF;
    --slider-height: 8px;
    --slider-vertical-height: 300px;
    --slider-radius: 2px;

    --slider-handle-bg: #7481FF;
    --slider-handle-border: 1px solid #FFFFFF;
    --slider-handle-width: 18px;
    --slider-handle-height: 18px;
    --slider-handle-radius: 0px;
    --slider-handle-shadow: none;
    --slider-handle-shadow-active: 0.5px 0.5px 2px 1px rgba(0, 0, 0, .42);
    --slider-handle-ring-width: 3px;
    --slider-handle-ring-color: #10B98130;

}

.price-block {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    .info-price-block {
        display: flex;
        justify-content: space-between;
        @include titleH5;
        margin-bottom: 10px;

        .year {
            display: flex;
            gap: 5px;
        }
    }
}

.tabs-item {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    margin-left: 10px;
    margin-top: 10px;
    gap: 10px;
    background: rgba(116, 129, 255, 0.2);
    padding: 10px;
    border-radius: 2px;
    @include closeIc;
    @include SecondText;
    &:before {
        font-size: 10px;
        font-weight: 700;
        color: #7481FF;
    }
}
</style>