<template>
    <title-background-component>Vehicle videos</title-background-component>
    <div class="page content-container">
        <div class="video-posts">
            <div class="post" v-for="(post, index) in videoPosts" :key="index">
                <video-post-component :video="post" @click="openModal(post.video)"></video-post-component>
            </div>
            <modal-component :showVideo=selectVideo :showModal=showModal  @closeModal="closeModal"></modal-component>
        </div>
    </div>
</template>
<script>
import titleBackgroundComponent from '@/components/title-background-component.vue';
import videoPostComponent from '@/components/posts/video-post-component.vue';
import videoPosts from '@/js/video-posts';
import modalComponent from '@/components/modal-component.vue';
export default ({
    data() {
        return {
            videoPosts,
            showModal: false,
            selectVideo: "sssssss",
        }
    },
    components: {
        titleBackgroundComponent,
        videoPostComponent,
        modalComponent,
    },
    methods:{
        closeModal(){
            this.showModal = false;
        },
        openModal(video){
            this.showModal = true;
            this.selectVideo = video;
            document.body.style.overflowY = 'hidden'
        }
    }
})
</script>

<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.video-posts {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    .post {
        width: 100%;
        height: 100%;
    }
}
</style>
