<template>
    <div class="postid-page" id="post">
        <div class="img-post-block">
            <img class="img" :src="selectedItem.src" alt="img-post">
        </div>

        <div class="page-container">
            <div>
                <router-link class="btn" to="/Blog/">
                    <back-control :blog="true" class="back-сontrol"></back-control>
                </router-link>
                <div class="content-post-block">
                    <div class="date-btn-block">
                        <router-link class="btn" to="/Blog/">
                            <back-control :blogMobile="true" class="back-сontrol"></back-control>
                        </router-link>
                        <p class="date">{{ selectedItem.date }}</p>
                    </div>
                    <h2 class="title">{{ selectedItem.title }}</h2>
                    <div class="content" v-html="selectedItem.content"></div>
                </div>
            </div>
            <swiper-component :hidenElement="route">You might like it</swiper-component>
        </div>
    </div>
</template>
  
<script>
import { useRoute } from 'vue-router'
import posts from '@/js/blog-posts.js';
import backControl from '@/components/ui/back-control.vue';
import swiperComponent from '@/components/swiper-component.vue';
export default {
    name: 'post-page',
    data() {
        return {
            posts,
        }
    },
    components: {
        backControl,
        swiperComponent,
    },
    computed: {
        selectedItem() {
            return this.posts.find(item => item.id === this.route);
        },
        route(){
            return Number(useRoute().params.id)
        }
    },
}
</script>
<style scoped lang="scss">
@import '@/assets/scss/pages/post-id-page/post-id-page.scss';
</style>
