<template>
    <div class="contact-form">
        <burger-button isOpen @click="$emit('changeComponent', 'menuComponent')" ></burger-button>
        <h2 class="contactTitle">We can't wait to hear from you!</h2>
        <form class="contactForm" @submit.prevent="submitForm">
            <div class="contactInputBlock vehicleInputBlock">
                <input type="text" name="" class="contactInput" :class="{ error: v$.vehicle.$error }"
                    placeholder="Name of selected vehicle" v-model="vehicle">
            </div>
            <div class="input-errors" v-for="error of v$.vehicle.$errors" :key="error.$uid">
                <div class="error-msg">{{ error.$message }}</div>
            </div>
            <div class="contactInputBlock">
                <span class="name"></span>
                <input type="text" id="name" class="contactInput" :class="{ error: v$.name.$error }" placeholder="Your Name"
                    v-model="name">
            </div>
            <div class="input-errors" v-for="error of v$.name.$errors" :key="error.$uid">
                <div class="error-msg">{{ error.$message }}</div>
            </div>
            <div class="contactInputBlock">
                <span class="phone"></span>
                <input type="text" class="contactInput" :class="{ error: v$.phone.$error }" placeholder="Phone Number"
                    v-model="phone">
            </div>
            <div class="input-errors" v-for="error of v$.phone.$errors" :key="error.$uid">
                <div class="error-msg">{{ error.$message }}</div>
            </div>
            <div class="contactInputBlock">
                <span class="mail"></span>
                <input type="text" class="contactInput" :class="{ error: v$.email.$error }" placeholder="Email Address"
                    v-model="email">
            </div>
            <div class="input-errors" v-for="error of v$.email.$errors" :key="error.$uid">
                <div class="error-msg">{{ error.$message }}</div>
            </div>
            <base-button background class="contactButton" type="submit">contact me</base-button>
        </form>
        <div class="weSocialBlock">
            <h2 class="socialTitle">You can contact us yourself</h2>
            <div class="socialBlock">
                <base-social-button background class="instagram" href="https://www.instagram.com/"></base-social-button>
                <base-social-button background class="facebook" href="https://www.facebook.com/"></base-social-button>
                <base-social-button background class="youtube" href="https://www.youtube.com/"></base-social-button>
                <base-social-button background class="mail" href="https://mail.google.com/"></base-social-button>
            </div>
        </div>
    </div>
</template>
<script>
import { useVuelidate } from '@vuelidate/core'
import { required, email } from '@vuelidate/validators'
import baseButton from '../ui/base-button.vue'
import baseSocialButton from '../ui/base-social-button.vue'
import BurgerButton from '../ui/burger-button.vue'

export default {
    components: { baseButton, baseSocialButton, BurgerButton },
    setup() {
        return {
            v$: useVuelidate()
        }
    },
    data() {
        return {
            vehicle: '',
            name: '',
            phone: '',
            email: '',
        }
    },
    validations() {
        return {
            vehicle: { required },
            name: { required },
            phone: { required },
            email: { required, email }
        }
    },
    methods: {
        async submitForm() {
            const isForm = await this.v$.$validate()
            if (isForm) {
                this.vehicle = '';
                this.name = '';
                this.phone = '';
                this.email = '';
                this.v$.$reset();
                this.$emit('changeComponent', 'completeComponent');
            }
        }
    }
}
</script>
<style lang="scss" scoped>
@import '@/assets/scss/components/drawer/contact-form-component.scss';
</style>