<template>
    <div class="filter-block">
        <div class="accordion" :class="{ open: this.open }">
            <div class="accordion-header" @click="OpenAccordion" :class="{ open: this.open }">
                <p class="input-text">{{ title }}</p>
                <span class="arrow" :class="{ open: this.open }"></span>
            </div>
            <transition name="accordion" @enter="startOpen" @after-enter="end" @before-leave="startClose"
                @after-leave="end">
                <div class="accordion-body" v-if=open>
                    <slot name="body1">

                    </slot>
                </div>

            </transition>
            <slot name="tabs"></slot>
            <transition name="accordion" @enter="startOpen" @after-enter="end" @before-leave="startClose"
                @after-leave="end">
                <div class="accordion-body" v-if=open>
                    <slot name="body2">

                    </slot>
                </div>
            </transition>
            <slot name="tabs2" class="tabs"></slot>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            open: false
        }
    },
    props: {
        title: {
            type: String,
        },
    },
    methods: {
        OpenAccordion() {
            this.open = !this.open;
        },
        startOpen(el) {
            el.style.height = '0';
            setTimeout(() => {
                el.style.height = el.scrollHeight + 'px';
            }, 0);
        },
        startClose(el) {
            el.style.height = el.scrollHeight + 'px';
        },
        end(el) {
            el.style.height = "";
        }
    },
    computed: {

    }

};
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";

.accordion {
    border: 1px solid #d7d7d7;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    &.open{
        padding-bottom: 20px;
    }
    .accordion-header {
        cursor: pointer;
        display: flex;
        justify-content: space-between;
        padding-left: 10px;
        padding-right: 10px;
        gap: 10px;
        align-items: center;
        position: relative;
        color: #41456b;
        @include input-text;

        &.open {
            .arrow {
                transform: rotate(180deg);
            }
        }

        .arrow {
            font-size: 25px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include arrow-bottomIc;
            transition: all 0.5s;
        }
    }

    .accordion-body {
        @include description;
        color: #606276;
        padding-left: 10px;
        padding-right: 10px;
        .text-block {
            padding-top: 10px;
        }
    }

    .accordion {
        display: flex;
        justify-content: space-between;
    }

    .accordion-enter-active,
    .accordion-leave-active {
        will-change: height, opacity;
        transition: height 1s ease, opacity 0.4s ease;
        overflow: hidden;
    }

    .accordion-enter,
    .accordion-leave-to {
        height: 0 !important;
        opacity: 0;
    }

    .tabs{
        padding-left: 10px;
        padding-right: 20px;
    }

}
</style>