<template>
    <div class="calculatorComponentBlock container">
        <div class="calculatorFormBlock">
            <div class="formCalcBlock">
                <div class="radioButtons">
                    <button @click="radioChange(1)" :class="{ active: radioButton1 }"
                        class="radioButton buttonText">Poor</button>
                    <button @click="radioChange(2)" :class="{ active: radioButton2 }"
                        class="radioButton radioButton2 buttonText">Average</button>
                    <button @click="radioChange(3)" :class="{ active: radioButton3 }"
                        class="radioButton buttonText">Good</button>
                </div>
                <div class="amountRangeBlock rangeBlock">
                    <div class="infoBlock">
                        <p class="titleText">Loan Amount</p>
                        <p class="titleText">$ {{ amount }}</p>
                    </div>
                    <input class="rangeInput styled-slider slider-progress" type="range" step="1000" v-model="amount"
                        :min="minAmount" :max="maxAmount" @input="rangeChange" id="price" name="price"/>
                </div>
                <div class="durationRangeBlock rangeBlock">
                    <div class="infoBlock">
                        <p class="titleText">Loan Duration</p>
                        <p class="titleText">{{ duration }}<span class="SecondText">month</span></p>
                    </div>
                    <input class="rangeInput styled-slider slider-progress" type="range" step="1" min="6" max="60"
                        v-model="duration" @input="rangeChange" id="months" name="months"/>
                </div>
            </div>
            <div class="ressCalcBlock">
                <div class="infoBlock">
                    <div class="biWeekPayBlock">
                        <p class="description">Bi-Weekly Payment</p>
                        <h2 class="titleH2">$ {{ Math.ceil(beWeekPay) }}</h2>
                    </div>
                    <div class="elsePayBlock">
                        <div class="monthlyPayBlock">
                            <p class="description">Monthly Payment</p>
                            <h4 class="titleH4">$ {{ Math.ceil(monthPay) }}</h4>
                        </div>
                        <div class="weeklyPayBlock">
                            <p class="description">Weekly Payment</p>
                            <h4 class="titleH4">$ {{ Math.ceil(weekPay) }}</h4>
                        </div>
                    </div>
                </div>
                <base-button background class="button button-background">Request a car</base-button>
            </div>
        </div>
    </div>
</template>
<script>
import baseButton from '../ui/base-button.vue';
export default {
  components: { baseButton },
    data() {
        return {
            amount: 30000,
            minAmount: 1000,
            maxAmount: 100000,
            duration: 60,
            radioButton1: true,
            radioButton2: false,
            radioButton3: false,
        }
    },
    mounted() {
        this.progresFunc();
    },
    methods: {

        async radioChange(number) {

            if (number == 1) {
                this.amount = 30000;
                this.duration = 60;
            }
            else if (number == 2) {
                this.amount = 56000;
                this.duration = 37;
            }
            else if (number == 3) {
                this.amount = 100000;
                this.duration = 20;
            }
            await this.ifFunc();
            this.progresFunc();
        },
        async ifFunc() {
            if (this.monthPay <= 1000) {
                this.radioButton1 = true;
                this.radioButton2 = false;
                this.radioButton3 = false;
            }
            else if (this.monthPay > 1000 && this.monthPay <= 4000) {
                this.radioButton1 = false;
                this.radioButton2 = true;
                this.radioButton3 = false;
            }
            else if (this.monthPay > 4000) {
                this.radioButton1 = false;
                this.radioButton2 = false;
                this.radioButton3 = true;
            }
        },
        async progresFunc() {
            for (let e of document.querySelectorAll('input[type="range"].slider-progress')) {
                e.style.setProperty('--value', e.value);
                e.style.setProperty('--min', e.min == '' ? '0' : e.min);
                e.style.setProperty('--max', e.max == '' ? '100' : e.max);
                e.addEventListener('input', () => e.style.setProperty('--value', e.value));
            }
        },
        async rangeChange() {
            await this.ifFunc();
            this.progresFunc();
        },

    },
    computed: {
        monthPay() {
            return (this.amount / this.duration);
        },
        weekPay() {
            return this.amount / (this.duration * 4);
        },
        beWeekPay() {
            return this.amount / (this.duration * 2);
        },
    }
}
</script>
<style scoped lang="scss">
@import '@/assets/scss/components/calculator/calculator-component.scss';
</style>