<template>
    <div class="employment-status-block">
        <h4 class="titleH4">Enter your monthly income</h4>
        <p class="description">Your income details help us make sure your vehicle payments are easy and affordable. Before
            taxes and deductions.
        </p>
        <div class="input-block">
            <input class="input" v-model="value" placeholder="Monthly income">
        </div>
        <div class="buttons-block">
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            value: '',
            progress: 30,
        }
    },
    activated() {
        this.$emit('progress', this.progress);
    },
    computed: {
        disabled() {
            if (this.value != '') return false;
            else return true;
        },
    },
    methods: {
        selectValue(value) {
            if (value != "") {
                this.disabled = false;
            }
            this.input = value;
        },
        sendValue() {
            this.$emit('earning', this.value);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>