<template>
    <accordion-component :title="'Body type'">
        <template #body1>
            <ul class="body-type-list">
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[0].value" @change="updateFilter" type="checkbox" id="Trucks"><label
                        class="description-checkbox" for="Trucks"><img src="@/assets/img/svg/Truck.svg"
                            alt="Truck">Trucks</label>
                </li>
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[1].value" @change="updateFilter" type="checkbox" id="SUV"><label
                        class="description-checkbox" for="SUV"><img src="@/assets/img/svg/SUV.svg" alt="SUV">SUV</label>
                </li>
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[2].value" @change="updateFilter" type="checkbox" id="Sedan"><label
                        class="description-checkbox" for="Sedan"><img src="@/assets/img/svg/Sedan.svg"
                            alt="Sedan">Sedan</label>
                </li>
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[3].value" @change="updateFilter" type="checkbox" id="Hatchback"><label
                        class="description-checkbox" for="Hatchback"><img src="@/assets/img/svg/Hatchback.svg"
                            alt="Hatchback">Hatchback</label>
                </li>
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[4].value" @change="updateFilter" type="checkbox" id="Coupe"><label
                        class="description-checkbox" for="Coupe"><img src="@/assets/img/svg/Coupe.svg"
                            alt="Coupe">Coupe</label>
                </li>
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[5].value" @change="updateFilter" type="checkbox" id="Convertiable"><label
                        class="description-checkbox" for="Convertiable"><img src="@/assets/img/svg/Convertiable.svg"
                            alt="Convertiable">Convertiable</label>
                </li>
                <li class="body-type-item">
                    <input class="checkbox" v-model="bodyTypeArr[6].value" @change="updateFilter" type="checkbox" id="VAN"><label
                        class="description-checkbox" for="VAN"><img src="@/assets/img/svg/VAN.svg" alt="VAN">VAN</label>
                </li>
            </ul>
        </template>
        <template #tabs v-if="bodyTypeArr.length">
            <div style="display: inline" v-for="(result, index) in bodyTypeArr" :key="index">
                <div class="tabs-item" v-if="result.value != false" @click="remove(result.id)">{{ result.title }}</div>
            </div>
        </template>
    </accordion-component>
</template>
<script>

import accordionComponent from '@/components/inventory-page/accordion-component.vue'
export default {
    components: { accordionComponent },
    data() {
        return {
            bodyTypeArr: [
                { id: 1, title: "Trucs", value: false },
                { id: 2, title: "SUV", value: false },
                { id: 3, title: "Sedan", value: false },
                { id: 4, title: "Hatchback", value: false },
                { id: 5, title: "Coupe", value: false },
                { id: 6, title: "Convertiable", value: false },
                { id: 7, title: "VAN", value: false },
            ],
        }
    },
    inject: ['bodyType', 'setBodyType'],
    methods: {
        updateFilter(){
            this.setBodyType(this.bodyTypeArr.filter(item => item.value !== false).map(item => item.title));
        },
        remove(id) {
            
            const index = this.bodyTypeArr.findIndex(item => item.id === id);
            if (index > -1) {
                this.bodyTypeArr[index].value = false;
                console.log(this.bodyTypeArr[index].value);
            }
            this.updateFilter();
            return this.bodyTypeArr;
        },
    }
}
</script>
<style scoped lang="scss">
@import "@/assets/scss/default/default.scss";

.body-type-list {

    display: flex;
    flex-direction: column;
    gap: 10px;

    &:first-child {
        margin-top: 20px;
    }

    .body-type-item {
        display: flex;
        align-items: center;
        gap: 10px;

        .checkbox {
            position: relative;
            max-width: 20px;
            max-height: 20px;
            width: 20px;
            height: 20px;
            border: none;
            border-radius: 2px;
            cursor: pointer;

            &::before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                border: 1px solid #D7D7D7;
                border-radius: 2px;

            }

            &:checked {
                &::before {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    border: 1px solid #D7D7D7;
                    background-image: url('@/assets/img/svg/checkbox.svg');
                    background-color: #ffffff;
                    background-position: 1px 2px;
                    background-repeat: no-repeat;
                    border-radius: 2px;
                }
            }
        }

        .description-checkbox {
            display: flex;
            gap: 5px;
            @include input-text;
        }
    }
}

.tabs-item {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    margin-left: 10px;
    margin-top: 10px;
    gap: 10px;
    background: rgba(116, 129, 255, 0.2);
    padding: 10px;
    border-radius: 2px;
    @include closeIc;
    @include SecondText;

    &:before {
        font-size: 10px;
        font-weight: 700;
        color: #7481FF;
    }
}
</style>