<template>
    <div class="pag">
    <div class="pagination-block">
        <div class="post" v-for="(post, index) in paginatedData" :key="index">
            <PostC :id="post.id" :title="post.title" :date="post.date" :src="post.src"></PostC>
        </div>

    </div>
    <div class="pagination-button">
        <button class="button-rectangle" :disabled="currentPage === 1" @click="prevPage()"></button>
        <button class="button-rectangle" :disabled="currentPage === totalPages" @click="nextPage()"></button>
    </div>
</div>
</template>
<script>
import PostC from '@/components/posts/blog-post-component.vue';
export default {
    data() {
        return {
            screenWidth: window.innerWidth,
            currentPage: 1,
        }
    },
    props: {
        data: {
            type: Array,
            required: true
        },
    },
    components: {
        PostC,
    },
    mounted() {
        window.addEventListener("resize", this.handleResize);
    },
    beforeUnmount() {
        window.removeEventListener("resize", this.handleResize);
    },
    computed: {
        paginatedData() {
            const startIndex = (this.currentPage - 1) * this.itemsPerPage;
            const endIndex = startIndex + this.itemsPerPage;
            return this.data.slice(startIndex, endIndex);
        },
        itemsPerPage() {
            if (this.screenWidth < 969 && this.screenWidth > 600) {
                return 8;
            } else if (this.screenWidth < 601) {
                return 6;
            } else {
                return 9;
            }

        },
        totalPages() {
            return Math.ceil(this.data.length / this.itemsPerPage);
        }
    },
    methods: {
        prevPage() {
            if (this.currentPage > 1) {
                this.currentPage--;
            }
        },
        nextPage() {
            if (this.currentPage < this.totalPages) {
                this.currentPage++;
            }
        },
        handleResize() {
            this.screenWidth = window.innerWidth;
        },
    }
};
</script>
<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.pagination-block {

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;

    @media (max-width: $deskToTablet) {
        grid-template-columns: repeat(2, 1fr);
    }

    @media (max-width: $tabletToMobile) {
        grid-template-columns: repeat(1, 1fr);
    }

    .post {
        min-height: 100%;
        min-width: 100%;
    }
}

.pag{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    gap: 40px;
    @media (max-width: 600px) {
        gap: 30px;
    }
}
.pagination-button {
    display: flex;
    justify-content: center;
    gap: 10px;

    .button-rectangle {
        border-radius: 50%;
        width: 10px;
        height: 10px;
        border: none;
        background-color:#606276;
        opacity: 0.3;
        cursor: pointer;
        &:disabled {
            background: #606276;
            opacity: 1;
            cursor:auto;
        }
    }
}
</style>