<template>
    <accordion-component title="Kilometres">
        <template #body1>
            <div class="kilometres-block">
                <div class="info-kilometres-block">
                    <p class="kilometres">{{ rangeSlider.value }} or less</p>
                </div>

                <Slider class="slider-range" v-model="rangeSlider.value" :min="0" :max="max" @input="this.rangeSlider.checked = true"
                    :tooltips="false" v-bind="rangeSlider" @change="updateFilter"></Slider>
            </div>
        </template>
        <template #tabs v-if="rangeSlider.checked == true">
                <div class="tabs-item" v-if="this.rangeSlider.checked != false"  @click="DelSelectKilometres">{{ this.rangeSlider.value }}</div>
        </template>
    </accordion-component>
</template>
<script>
import Slider from '@vueform/slider'
import accordionComponent from '@/components/inventory-page/accordion-component.vue'
export default {
    components: { accordionComponent, Slider },
    inject: ['kilometres', 'SetKilometres'],
    data() {
        return {
            max: 500000,
            rangeSlider: { id: 1, title: 'Kilometres', value: 500000, checked: false },
        }
    },
    methods:{
        updateFilter() {
                this.SetKilometres(this.rangeSlider);
        },
        DelSelectKilometres(){
            this.rangeSlider.value = 500000;
            this.rangeSlider.checked = false;
        }
    }
}
</script>
<style  scoped lang="scss">
@import "@/assets/scss/default/default.scss";
@import '@vueform/slider/themes/default.css';

.slider-range {
    margin-bottom: 10px;
    cursor: pointer;
    --slider-bg: #D7D7D7;
    --slider-connect-bg: #7481FF;
    --slider-connect-bg-disabled: #7481FF;
    --slider-height: 8px;
    --slider-vertical-height: 300px;
    --slider-radius: 2px;

    --slider-handle-bg: #7481FF;
    --slider-handle-border: 1px solid #FFFFFF;
    --slider-handle-width: 18px;
    --slider-handle-height: 18px;
    --slider-handle-radius: 0px;
    --slider-handle-shadow: none;
    --slider-handle-shadow-active: 0.5px 0.5px 2px 1px rgba(0, 0, 0, .42);
    --slider-handle-ring-width: 3px;
    --slider-handle-ring-color: #10B98130;

}

.kilometres-block {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    .info-kilometres-block {
        display: flex;
        justify-content: space-between;
        @include titleH5;
        margin-bottom: 10px;

        .kilometres {
            display: flex;
            gap: 5px;
        }
    }
}

.tabs-item {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    margin-left: 10px;
    margin-top: 10px;
    gap: 10px;
    background: rgba(116, 129, 255, 0.2);
    padding: 10px;
    border-radius: 2px;
    @include closeIc;
    @include SecondText;
    &:before {
        font-size: 10px;
        font-weight: 700;
        color: #7481FF;
    }
}
</style>