<template>
    <button class="backControls" :class="backControlClass">
        <span class="icon"></span>
        <span class="text">back</span>
    </button>
</template>
<script>

export default {
    name: 'back-control',
    props: {
        blog:{
            type: Boolean,
            default: false,
        },
        blogMobile:{
            type: Boolean,
            default: false,
        },
    },
    computed: {
        backControlClass() {
            return {
                "display-blog": this.blog,
                "display-blog-mobile": this.blogMobile
            }
        }
    },


}

</script>
<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.backControls {
  padding: 0;
  color: $color1;
  display: flex;
  align-items: center;
  background: none;
  border: none;
  gap: 10px;
  cursor: pointer;
  &:hover {
    color: $color2;
  }

  .icon {
    @include arrow-backIc;
    display: block;
    transform: rotate(-90deg);
    font-size: 22px;
  }
  .text {
    @include buttonText;
  }
  &.display-blog-mobile {
    display: none;
    @media (max-width: 600px) {
      display: flex;
    }
  }

  &.display-blog {
    position: relative;
    top: 140px;
    left: 0;
    z-index: 3;
    @media (max-width: 1200px) {
      .text {
        display: none;
      }
    }
    @media (max-width: $deskToTablet) {
      top: 120px;
    }
    @media (max-width: 600px) {
      display: none;
    }
  }
}

</style>