<template>
    <div class="page content-container">
        <div class="filter-block">
            <filter-component></filter-component>
        </div>
        <div class="products-block">
            <div class="navigation-block">
                <div class="search-block">
                    <div class="input-block">
                        <input type="text" placeholder="Find a dream car..." id="input-navigation" class="input-navigation">
                        <label for="input-navigation" class="icon"></label>
                    </div>
                    <button class="button-icon"></button>
                </div>
                <div class="sort-block">
                    <label class="sort-label" for="sort">Sorted by</label>
                    <div class="select-sort-block">
                        <select class="select-sort" name="" id="sort" v-model="selectedOption"
                            @click="this.selectSort = !this.selectSort" @blur="this.selectSort = false">
                            <option value="recommendations" selected>Recommendations</option>
                            <option value="newest-inventory">Newest inventory</option>
                            <option value="lowest-price">Lowest price</option>
                            <option value="highest-price">Highest prices</option>
                        </select>
                        <span class="arrow" :class="{ reverse: selectSort }"></span>
                    </div>
                </div>
            </div>
            <pagination-component :data="filteredItems"></pagination-component>
        </div>
    </div>
</template>
<script>
import filterComponent from '@/components/filter-component.vue';
import carPosts from '@/js/car-posts';
import paginationComponent from '@/components/inventory-page/pagination-component.vue';

export default ({
    components: { filterComponent, paginationComponent },
    data() {
        return {
            carPosts,
            selectSort: false,
            selectedOption: "recommendations",
            searchText: "",
            selectedCategory: "",
            categories: ["category 1", "category 2", "category 3"],
            bodyType: '',
            transmission: '',
            priceS: { id: 1, title: 'Price', value: [3500, 150000], checked: false },
            year: { id: 1, title: 'Year', value: [1980, 2023], checked: false },
            kilometres: { id: 1, title: 'Kilometres', value: 500000, checked: false },
        }
    },
    provide() {
        return {
            transmission: this.transmission,
            bodyType: this.bodyType,
            priceS: this.priceS,
            year: this.year,
            kilometres: this.kilometres,
            SetKilometres: (kilometres) => {
                this.kilometres = kilometres
            },
            SetYear: (year) => {
                this.year = year
            },
            SetPrice: (price) => {
                this.priceS = price
            },
            setBodyType: (bodyType) => {
                this.bodyType = bodyType
            },
            setTransmission: (transmission) => {
                this.transmission = transmission
            }
        }
    },
    computed: {
        filteredItems() {
            console.log(this.bodyType);

            let filteredMass = this.carPosts.filter(item => item.price >= this.priceS.value[0] && item.price <= this.priceS.value[1] && item.year >= this.year.value[0] && item.year <= this.year.value[1] && item.kilometres <= this.kilometres.value);
            if (this.selectedOption === "newest-inventory") {
                filteredMass.sort((a, b) => new Date(a.dateAdd) - new Date(b.dateAdd));
            }
            else if (this.selectedOption === "lowest-price") {
                filteredMass.sort((a, b) => a.price - b.price);
            }
            else if (this.selectedOption === "highest-price") {
                filteredMass.sort((a, b) => b.price - a.price);
            }
            if (this.transmission.length) {
                filteredMass = filteredMass.filter(item => item.transmission.includes(this.transmission[0]) || item.transmission.includes(this.transmission[1]));
            }
            if (this.bodyType.length) {
                filteredMass = filteredMass.filter(item => item.type.includes(this.bodyType[0]) || item.type.includes(this.bodyType[1]) || item.type.includes(this.bodyType[2]) || item.type.includes(this.bodyType[3]) || item.type.includes(this.bodyType[4]) || item.type.includes(this.bodyType[5]));
            }
            return filteredMass;
        },
    },
})
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";

.page {
    display: flex;
    gap: 40px;
}

.filter-block {
    max-width: 300px;
    width: 100%;
}

.navigation-block {
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    .sort-block {
        display: flex;
        align-items: center;
        width: max-content;
        gap: 20px;

        .sort-label {
            @include description;
            cursor: pointer;
        }

        .select-sort-block {
            position: relative;

            .arrow {
                @include arrow-bottomIc;
                position: absolute;
                right: 10px;
                padding-top: 10px;
                padding-bottom: 10px;
                font-size: 24px;
                transition: all 0.5s;

                &.reverse {
                    transform: rotate(180deg);
                }
            }

            .select-sort {
                padding: 8px 40px 8px 20px;
                width: 237px;
                border: 1px solid #D7D7D7;
                -webkit-appearance: none;
                -moz-appearance: none;
                appearance: none;
                cursor: pointer;
            }
        }
    }

    .search-block {
        width: max-content;
        display: flex;
        align-items: center;

        .input-block {
            position: relative;
            display: inline-block;
            margin-right: 20px;

            .input-navigation {
                padding: 9px 44px 9px 20px;
                width: 350px;
                border: 1px solid #D7D7D7;
            }

            .icon {
                @include searchIc;
                position: absolute;
                right: 20px;
                top: 50%;
                transform: translate(0, -50%);
                font-size: 24px;
                cursor: pointer;
            }
        }

        .button-icon {
            border: none;
            background: none;
            @include shareIc;
            font-size: 22px;
            cursor: pointer;
        }
    }
}

.car-posts-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;

    .post {
        width: 100%;
        height: 100%;
    }
}

.product-block {}
</style>