<template>
    <router-link  :to=openPost class="car-post-block">
        <swiper class="swiper" :modules="modules" :pagination="{ type: 'progressbar' }" :loop="true">
            <swiper-slide class="slide" v-for="(result, index) in item.Exterior" :key="index"><img :src="result"
                    alt="">

            </swiper-slide>

        </swiper>
        <div class="text-block">
            <p class="title">
                {{ item.title }}
            </p>
            <p class="price">$ {{ item.price }}</p>
            <div class="info-car-block">
                <span class="year info-car-text">{{ item.year }} year</span>
                <span class="bodyType info-car-text">{{ item.type }}</span>
                <span class="transmission info-car-text">{{ item.transmission }}</span>
                <span class="kilometres info-car-text">{{  item.kilometres }}</span>
            </div>
        </div>
    </router-link>
</template>
<script>
import { Pagination } from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import carPosts from '@/js/car-posts'

export default {
    setup() {
        return {
            modules: [Pagination]
        }
    },
    name: 'post-car',
    components: {
        Swiper, SwiperSlide,
    },
    data() {
        return {
            carPosts,
        }
    },
    props: {
        item:{
            type: Object,
        }
    },
    computed: {
        openPost() {
            return `/inventory/${this.item.id}`;
        },
    },

}
</script>

<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.car-post-block {
    display: flex;
    flex-direction: column;
    max-width: 420px;
    width: 100%;
    height: 100%;
    box-shadow: 0px 20px 25px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    overflow: hidden;

    .swiper {
        position: relative;
        width: 100%;
        max-height: 217px;
        overflow: hidden;
        height: 100%;
        ::v-deep(.swiper-pagination-horizontal) {
            margin-left: 5%;
            margin-right: 5%;
            border-radius: 100rem;
            background-color: rgba($color: #ffff, $alpha: .2);
            position: absolute;
            bottom: 5px;
            max-height: 5px;
            top: auto;
            width: 90%;
            height: 100%;

            .swiper-pagination-progressbar-fill {
                border-radius: 100rem;
                background-color: rgba($color: #ffff, $alpha: .7);
                max-height: 5px;
                height: 100%;
            }
        }


        .slide {
            overflow: hidden;
            background-color: $color3;
            height: 217px;
            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
    }

    .text-block {
        padding: 20px;

        .title {
            @include titleH5;
            color: #41456B;
        }

        .price {
            margin-top: 10px;
            @include titleH5;
            color: #7481FF;
        }

        .info-car-block {
            margin-top: 20px;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            column-gap: 40px;
            @include description;
            overflow: hidden;
            color: #41456B;

            .info-car-text {
                position: relative;

                &::before {
                    margin-top: auto;
                    margin-bottom: auto;
                    position: absolute;
                    left: -40px;
                    top: 50%;
                    transform: translate(0, -50%);
                    content: "";
                    width: 1px;
                    height: 19px;
                    background-color: #D7D7D7;
                }
            }
        }
    }
}</style>
