<template>
    <div class="employment-status-block">
        <h4 class="titleH4">What's your employment status?</h4>
        <p class="description">Your employment status will help determine the best vehicle and financing options for you.
        </p>
        <div class="buttons-block">
            <base-button @click="selectValue(1)" :class="{ 'select': value == 1 }" quiz>Employed</base-button>
            <base-button @click="selectValue(2)" :class="{ 'select': value == 2 }" quiz>Self-Employed</base-button>
            <base-button @click="selectValue(3)" :class="{ 'select': value == 3 }" quiz>Student</base-button>
            <base-button @click="selectValue(4)" :class="{ 'select': value == 4 }" quiz>Retired / Pension</base-button>
            <base-button @click="selectValue(5)" :class="{ 'select': value == 5 }" quiz>Other</base-button>
            <div></div>
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
            <backControl @click="back"></backControl>
        </div>
    </div>
</template>
<script>
import backControl from '../ui/back-control.vue';
import baseButton from '../ui/base-button.vue'
export default {
    components: { baseButton, backControl },
    data() {
        return {
            value: 0,
            disabled: true,
            progress: 20,
        }
    },
    activated() {
        this.$emit('progress', this.progress);
    },
    methods: {
        selectValue(id) {
            this.disabled = false;
            this.value = id;
        },
        sendValue() {
            this.$emit('employment-status', this.value);
        },
        back() {
            this.$emit('back');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>