<template>
    <router-link  :to=openPost class="post-block">
        <img :src=src alt="img">
        <div class="text-block">
            <p class="date">
                {{ date }}
            </p>
            <p class="title">{{ title }}</p>
        </div>
    </router-link>
</template>
<script>

export default {
    name: 'post-component',
    props: {
        id: {
            type: Number,
            default: 0
        },
        date: {
            type: String,
            default: ' '
        },
        title: {
            type: String,
            default: ' '
        },
        src: {
            type: String,
            default: ' '
        }
    },
    computed: {
        openPost() {
            return `/Blog/${this.id}`;
        },
    },

}
</script>

<style scoped lang="scss">
@import '@/assets/scss/components/posts/blog-post-component.scss';
</style>
