<template>
    <accordion-component :title="'Transmission'">
        <template #body1>
            <ul class="transmission-list">
                <li class="transmission-item">
                    <input class="checkbox" v-model="transmissionTypeArr[0].value" @change="updateFilter" type="checkbox" id="Automatic"><label
                        class="description-checkbox" for="Automatic">Automatic</label>
                </li>
                <li class="transmission-item">
                    <input class="checkbox" v-model="transmissionTypeArr[1].value" @change="updateFilter" type="checkbox" id="Manual"><label
                        class="description-checkbox" for="Manual">Manual</label>
                </li>
            </ul>
        </template>
        <template #tabs v-if="transmissionTypeArr.length">
            <div style="display: inline" v-for="(result, index) in transmissionTypeArr" :key="index">
                <div class="tabs-item" v-if="result.value != false" @click="remove(result.id)">{{ result.title }}</div>
            </div>
        </template>
    </accordion-component>
</template>
<script>
import accordionComponent from '@/components/inventory-page/accordion-component.vue'
export default {
    components: { accordionComponent },
    inject: ['transmission', 'setTransmission'],
    props:{
        item:{
            type: Object,
        }
    },
    data() {
        return {
            transmissionTypeArr: [
                { id: 1, title: "Automatic", value: false },
                { id: 2, title: "Manual", value: false },
            ],
        }
    },
    methods: {
        updateFilter(){
            this.setTransmission(this.transmissionTypeArr.filter(item => item.value !== false).map(item => item.title));
        },
        remove(id) {
            const index = this.transmissionTypeArr.findIndex(item => item.id === id);
            if (index > -1) {
                this.transmissionTypeArr[index].value = false;
                console.log(this.transmissionTypeArr[index].value)
            }
            this.updateFilter();
            return this.transmissionTypeArr;
        }
    }
}
</script>
<style scoped lang="scss">
@import "@/assets/scss/default/default.scss";

.transmission-list {

    display: flex;
    flex-direction: column;
    gap: 10px;

    &:first-child {
        margin-top: 20px;
    }

    .transmission-item {
        display: flex;
        align-items: center;
        gap: 10px;

        .checkbox {
            position: relative;
            max-width: 20px;
            max-height: 20px;
            width: 20px;
            height: 20px;
            border: none;
            border-radius: 2px;
            cursor: pointer;

            &::before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                border: 1px solid #D7D7D7;
                border-radius: 2px;

            }

            &:checked {
                &::before {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    border: 1px solid #D7D7D7;
                    background-image: url('@/assets/img/svg/checkbox.svg');
                    background-color: #ffffff;
                    background-position: 1px 2px;
                    background-repeat: no-repeat;
                    border-radius: 2px;
                }
            }
        }

        .description-checkbox {
            display: flex;
            gap: 5px;
            @include input-text;
        }
    }
}

.tabs-item {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    margin-left: 10px;
    margin-top: 10px;
    gap: 10px;
    background: rgba(116, 129, 255, 0.2);
    padding: 10px;
    border-radius: 2px;
    @include closeIc;
    @include SecondText;

    &:before {
        font-size: 10px;
        font-weight: 700;
        color: #7481FF;
    }
}
</style>