<template>
    <router-link class="logo-link" :class="changeColor" to="/">
        <img class="img" :src=changeImg alt="Logo">
        VAC
    </router-link>
</template>
<script>
export default {
    name: "logo-link",
    data(){
        return{
            src:[require("@/assets/img/svg/logo.svg"),require("@/assets/img/svg/logoWhite.svg")]
        }
    },
    props: {
        white: {
            type: Boolean,
            default: false
        },
    },
    computed: {
        changeColor() {
            return { white: this.white }
        },
        changeImg(){
            if(this.white) return this.src[1];
            else return this.src[0];
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";


.logo-link {
    display: flex;
    align-items: center;
    @include logoText;
    color: $color3;
    gap: 3px;

    &:not(.white) {
        color: $color5;
    }

    .img{
        max-height: 32px;
        max-width: 50px;
        @media (max-width: $deskToTablet) {
            margin-bottom: -4px;
            max-width: 50px;
            max-height: 32px;
        }
        @media (max-width: $tabletToMobile) {
            max-height: 23px;
            max-width: 36px;
        }
    }
}
</style>