<template>
  <button :class="burgerButtonClass">
    <span></span>
  </button>
</template>
<script>
export default {
  name: 'burger-button',
  props: {
    isOpen: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    burgerButtonClass() {
      return {
        'active': this.isOpen,
      }
    }
  }
}

</script>
<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.hide {
  opacity: 0;
}

.modal-content{
  button{
    &.active::after,
    &.active::before {
      top: 0px;
      background: $color3;
      @media (max-width: $tabletToMobile) {
        top: 50%;
        left: 0;
        align-self: end;
      }
    }
  }
}

.contact-form {
  button {
    position: absolute;
    top: 24px;
    max-width: 30px;
    padding: 0;
    &::before,
    &::after,
    span {
      height: 3px;
      left: -5px;
    }
    }
  }

  .complete-component{
    button {
    position: absolute;
    top: 24px;
    max-width: 30px;
    padding: 0;
    &::before,
    &::after,
    span {
      height: 3px;
      left: -5px;
    }
    @media (max-width: 500px) {
      top: 10px;
    }
    }
    
  }

  button {
    background: none;
    border: none;
    display: block;
    position: relative;
    width: 37px;
    height: 30px;
    z-index: 3;
    cursor: pointer;
    background: rgba(255, 255, 255, 1);
    transition: all 0ms 300ms;

    @media (max-width: $tabletToMobile) {
      &:not(.active) {
        width: 30px;
        height: 25px;
      }
    }

    &.animate {
      background: rgba(255, 255, 255, 0);
    }

    &::before,
    &::after,
    span {
      background: #41456b;
      border-radius: 7px;
      position: absolute;
      width: 100%;
      height: 4px;
      left: 0;
      transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1),
        transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
    }

    &::before,
    &::after {
      content: " ";
    }

    span {
      top: 50%;
      transform: translate(0, -50%);
    }

    &::before {
      bottom: 0;
    }

    &::after {
      top: 0;
    }

    &.active span {
      display: none;
    }

    &.active::after,
    &.active::before {
      top: 50%;
      transform: rotate(45deg);

      @media (max-width: $tabletToMobile) {
        height: 3px;
      }
    }

    &.active::after {
      transform: rotate(-45deg);
    }
  }
</style>