<template>
    <div class="accordionb">
        <div class="accordion-header" @click="OpenAccordion" :class="{ open: this.open }">
            {{ item.title }}
            <span class="arrow" :class="{ open: this.open }"></span>
        </div>
        <transition name="accordion" @enter="startOpen" @after-enter="end" @before-leave="startClose" @after-leave="end">
            <div class="accordion-body" v-if=open>
                <div class="text-block">{{ item.content }}</div>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
    data() {
        return {
            open: false
        }
    },
    props: {
        item: {
            type: Object,
        },
    },
    methods: {
        OpenAccordion() {
            this.open = !this.open;
        },
        startOpen(el) {
            el.style.height = '0';
            setTimeout(() => {
                el.style.height = el.scrollHeight + 'px';
            }, 0);
        },
        startClose(el){
            el.style.height = el.scrollHeight + 'px';
        },
        end(el) {
            el.style.height = "";
        }
    },
    computed: {

    }

};
</script>
<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.accordionb {
    border: 1px solid #d7d7d7;
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
    padding-top: 20px;

    .accordion-header {
        cursor: pointer;
        padding-left: 30px;
        padding-right: 30px;
        display: flex;
        justify-content: space-between;
        gap: 10px;
        align-items: center;
        position: relative;
        color: #41456b;
        @include titleText;

        &.open {
            .arrow {
                transform: rotate(180deg);
            }
        }

        .arrow {
            font-size: 25px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include arrow-bottomIc;
            transition: all 0.5s;
        }
    }

    .accordion-body {
        @include description;
        color: #606276;
        padding-right: 95px;
        padding-left: 30px;
        .text-block {
            padding-top: 10px;
        }
    }

    .accordion {
        display: flex;
        justify-content: space-between;
    }

    .accordion-enter-active,
    .accordion-leave-active {
        will-change: height, opacity;
        transition: height 1s ease, opacity 0.4s ease;
        overflow: hidden;
    }

    .accordion-enter,
    .accordion-leave-to {
        height: 0 !important;
        opacity: 0;
    }


}
</style>