<template>
    <accordion-component class="accordion-block" title="Make, Model">
        <template #body1>
            <label class="searchMake">Make</label>
            <div>
                <input class="input" autocomplete="off" @click="searchMakeFunc" type="text" id="searchMake"
                    v-model="searchMake" @focus="makeFocus = true" @blur="closeMake" placeholder="Search Make...">
                <transition name="accordion" @enter="startOpen" @after-enter="end" @before-leave="startClose">
                    <div class="block-list" v-if="searchMakeResults.length">
                        <ul class="serch-list" @click="makeFocus = true">
                            <li class="search-item" v-for="obj in searchMakeResults" @click="AddSelectMake(obj)"
                                :key="obj.id">{{
                                    obj.title }}</li>
                        </ul>
                    </div>
                </transition>
            </div>
        </template>
        <template #tabs v-if="selectMake.length">
            <div class="tabs-item" v-for="(obj, index) in selectMake" @click="DelSelectMake(obj)" :key="index">{{
                obj.title }}</div>
        </template>
        <template #body2>
            <label for="searchModel" class="label">Model</label>
            <div>
                <input class="input" type="text" id="searchModel" :disabled="disabledModelInput" v-model="searchModel"
                    @click="searchModelFunc" @focus="modelFocus = true" @blur="closeModel" placeholder="Search Model...">
                <transition name="accordion" @enter="startOpen" @after-enter="end" @before-leave="startClose">
                    <div class="block-list" v-if="searchModelResults.length">
                        <ul class="serch-list">
                            <li class="search-item" v-for="obj in searchModelResults" :key="obj.id"
                                @click="AddSelectModel(obj)">{{
                                    obj.title }}</li>
                        </ul>
                    </div>
                </transition>
            </div>
        </template>
        <template #tabs2 v-if="selectModel.length">
            <div class="tabs-item" v-for="obj in selectModel" @click="DelSelectModel(obj)" :key="obj.id">{{
                obj.title }}</div>
        </template>
    </accordion-component>
</template>
<script>
import modelList from '@/js/models.js'
import makeList from '@/js/cars.js'
import accordionComponent from '@/components/inventory-page/accordion-component.vue';
export default ({
    components: { accordionComponent, },
    data() {
        return {
            modelList,
            makeList,
            searchMake: '',
            searchModel: '',
            searchMakeResults: [],
            searchModelResults: [],
            modelForMake: [],
            selectMake: [],
            selectModel: [],
            makeFocus: false,
            modelFocus: false,
        }
    },
    computed: {
        disabledModelInput() {
            if (this.selectMake.length) {
                return false;
            }
            else {
                return true;
            }
        }
    },
    methods: {
        searchMakeFunc() {
            if (this.makeFocus && !this.searchMake) {
                this.searchMakeResults = this.makeList
            }
            else {
                this.searchMakeResults = this.makeList.filter(item =>
                    item.title.toLowerCase().includes(this.searchMake.toLowerCase())
                )
                if (this.searchMakeResults == 0) {
                    this.searchMakeResults = [{ title: 'Nothing found' }]
                }
            }
        },
        searchModelFunc() {
            if (this.selectMake.length && this.modelFocus) {
                try {
                    if (!this.searchModel) {
                        this.searchModelResults = this.modelForMake;
                    }
                    else {
                        this.searchModelResults = this.modelForMake.filter(item =>
                            item.title.toLowerCase().includes(this.searchModel.toLowerCase())
                        )
                        if (this.searchModelResults == 0) {
                            this.searchModelResults = [{ title: 'Nothing found' }]
                        }
                    }
                } catch (error) {
                    console.log('An error occurred while processing the models:', error);
                    this.searchModelResults = [];
                }
            }
            else {
                this.disabledModelInput = true;
            }
        },
        AddSelectMake(obj) {
            if (obj.id != undefined) {
                const indexAdd = this.selectMake.findIndex(el => el.id === obj.id);
                const indexDelete = this.makeList.findIndex(el => el.id === obj.id);
                if (indexAdd === -1) {
                    this.selectMake.push(obj);
                    this.makeList.splice(indexDelete, 1);
                    for (let i = 0; i < this.modelList.length; i++) {
                        if (this.modelList[i].makeId == obj.id) {
                            this.modelForMake.push(modelList[i]);
                        }
                    }
                }
            }
        },
        DelSelectMake(obj) {
            if (obj.id != undefined) {
                const indexAdd = this.makeList.findIndex(el => el.id === obj.id);
                const indexDelete = this.selectMake.findIndex(el => el.id === obj.id);
                if (indexAdd === -1) {
                    this.makeList.push(obj);
                    this.selectMake.splice(indexDelete, 1);
                    for (let i = 0; i < this.modelForMake.length; i++) {
                        if (this.modelForMake[i].makeId == obj.id) {
                            this.modelForMake.splice(i, 1);
                            i--;
                        }
                    }
                    for (let i = 0; i < this.selectModel.length; i++) {
                        if (this.selectModel[i].makeId == obj.id) {
                            this.selectModel.splice(i, 1);
                            i--;
                        }
                    }
                }
            }
        },
        AddSelectModel(obj) {
            if (obj.id != undefined) {
                const indexAdd = this.selectModel.findIndex(el => el.id === obj.id);
                const indexDelete = this.modelForMake.findIndex(el => el.id === obj.id);
                if (indexAdd === -1) {
                    this.selectModel.push(obj);
                    this.modelForMake.splice(indexDelete, 1);
                }
            }
        },
        DelSelectModel(obj) {
            if (obj.id != undefined) {
                const indexAdd = this.modelForMake.findIndex(el => el.id === obj.id);
                const indexDelete = this.selectModel.findIndex(el => el.id === obj.id);
                if (indexAdd === -1) {
                    this.modelForMake.push(obj);
                    this.selectModel.splice(indexDelete, 1);
                }
            }
        },
        closeMake() {
            this.searchMakeResults = [];
            this.makeFocus = false;
        },
        closeModel() {
            this.modelFocus = false;
            this.searchModelResults = [];
        },
        startOpen(el) {
            el.style.height = '0';
            setTimeout(() => {
                el.style.height = el.scrollHeight + 'px';
            }, 0);
        },
        startClose(el) {
            el.style.height = el.scrollHeight + 'px';
        },
        end(el) {
            el.style.height = "";
        },
    },
    watch: {
        searchMake: {
            handler() {
                this.searchMakeFunc();
            }
        },
        searchModel: {
            handler() {
                this.searchModelFunc();
            }
        },
    }
})
</script>
<style scoped lang="scss">
@import "@/assets/scss/default/default.scss";

.accordion-block {
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .searchMake {
        margin-top: 20px;
        display: block;
        margin-bottom: 3px;
    }

    .label {
        @include input-text;
        margin-top: 14px;
        display: block;
        width: fit-content;
    }

    .block-list {
        padding: 0;
        border: 1px solid #D7D7D7;

        .serch-list {
            overflow-y: auto;
            margin: 10px;

            max-height: 200px;

            &::-webkit-scrollbar {
                width: 10px;
                border-radius: 5px;
                border: 3px solid $color3;
            }

            &::-webkit-scrollbar-thumb {
                background-color: #333;
                border-radius: 10px;
                border: 10px solid #D7D7D7;

            }

            &::-webkit-scrollbar-track {
                background-color: #f1f1f1;
                border-radius: 10px;
                overflow: visible;
                border: 3px solid $color3;

            }

            li.search-item {
                padding: 10px;
                @include SecondText;
                cursor: pointer;
            }
        }
    }
}

.tabs-item {
    width: fit-content;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    margin-left: 10px;
    margin-top: 10px;
    gap: 10px;
    background: rgba(116, 129, 255, 0.2);
    padding: 10px;
    border-radius: 2px;
    @include closeIc;
    @include SecondText;

    &:before {
        font-size: 10px;
        font-weight: 700;
        color: #7481FF;
    }
}

.tabs-item:first-child {
    margin: 10px;
}

.input {
    border: 1px solid #D7D7D7;
    padding: 8px 20px;
    width: 100%;
}

.block-list {
    transition: all 0.5s;
}

.accordion-enter-active,
.accordion-leave-active {
    will-change: height, opacity;
    transition: height 1s ease, opacity 0.4s ease;
    overflow: hidden;
}

.accordion-enter,
.accordion-leave-to {
    height: 0 !important;
    opacity: 0;
}
</style>