<template>
    <div class="swiperC-block">
        <h2 class="title">
            <slot></slot>
        </h2>
        <router-link class="btn" to="/Blog/">
            <base-button class="button" :paddingMobile="'10px 36px 10px 36px'" :paddingDesktop="'16px 36px 15px 36px'">All articles
            </base-button>
        </router-link>
        <swiper class="swiper" :space-between="20" :breakpoints="{
            '0': {
                slidesPerView: 1,
                spaceBetween: 20
            },
            '601': {
                slidesPerView: 2,
                spaceBetween: 20
            },
            '969': {
                slidesPerView: 3,
                spaceBetween: 20
            }
        }">
            <swiper-slide class="slide" v-for="post in posts" :key="post.id"
                v-show="post.id != hidenElement">
                <blog-post-component class="post" :id="post.id" :title="post.title" :date="post.date" :src="post.src">
                </blog-post-component>
            </swiper-slide>

        </swiper>


    </div>
</template>
<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import posts from '@/js/array.js'
import baseButton from './ui/base-button.vue';
import blogPostComponent from './posts/blog-post-component.vue';
export default {
    name: 'swiper-block',
    data() {
        return {
            posts,
        }
    },
    props: {
        hidenElement: {
            type: Number,
            default: 0
        }

    },

    components: {
        Swiper,
        SwiperSlide,
        blogPostComponent,
        baseButton
    },
    
}

</script>

<style scoped lang="scss">
@import '@/assets/scss/default/default.scss';

.swiperC-block {
  display: grid;
  grid-template:
    "title btn"
    "slider slider";
  gap: 20px;
  row-gap: 60px;

  justify-content: space-between;
  align-items: center;
  @media (max-width: $tabletToMobile) {
    row-gap: 30px;
    grid-template:
      "title"
      "slider"
      "btn";
  }
  .btn {
    grid-area: "btn";
    justify-self: end;
    max-width: 180px;
    @media (max-width: $tabletToMobile) {
        justify-self: auto;
        max-width: none;
        width: 100%;
        
        .button{
            width: 100%;
            text-align: left;
        }
      }
  }
  .title {
    grid-column-start: 1;
    grid-column-end: 3;
    grid-area: "title";
    @include titleH2;
    @media (max-width: $tabletToMobile) {
      grid-column-start: 1;
      grid-column-end: 2;
    }
  }

  .swiper {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-area: "slider";
    margin: 0;
    filter: drop-shadow(0 20px 10px rgba(0, 0, 0, 0.1));
    @media (max-width: $tabletToMobile) {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 2;
        grid-row-end: 3;
      }
    .swiper-wrapper {
      height: min-content;
      .slide {
        height: auto;
        .post {
          min-height: 100%;
          min-width: 100%;
        }
      }
    }
  }
}

</style>
