<template>
    <div v-if="showModal" class="modal">
        <div class="close-modal" @click="closeModal"></div>
        <div class="modal-content">
            <iframe width="100%" height="100%" :src=showVideo frameborder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                allowfullscreen></iframe>
            <div class="button-block" @click="closeModal">
                <burger-button class="button" isOpen></burger-button>
            </div>
        </div>
    </div>
</template>
  
<script>
import burgerButton from './ui/burger-button.vue'
export default {
    components: { burgerButton },
    props: {
        showModal: {
            type: Boolean,
            default: false
        },
        showVideo: {
            type: String,
            default: '',
        },
    },
    methods: {
        closeModal() {
            this.$emit('closeModal', false);
            document.body.style.overflowY = 'auto';
        }
    }
}
</script>
  
<style scoped lang="scss">
@import "@/assets/scss/default/default.scss";

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;

    .close-modal {
        position: absolute;
        background: rgba(0, 0, 0, 0.1);
        width: 100%;
        height: 100%;
    }
}

.modal-content {
    pointer-events: visible;
    display: flex;
    align-items: start;
    gap: 10px;
    max-height: 543px;
    max-width: 901px;
    width: 100%;
    height: 100%;
    background: none;
    z-index: 5;
    margin-left: 24px;
    margin-right: 24px;

    @media (max-width: $deskToTablet) {
        max-height: 376px;
        max-width: 600px;
    }

    @media (max-width: $tabletToMobile) {
        max-height: 200px;
        max-width: 320px;
        flex-direction: column-reverse;

    }

    .button-block {
        display: flex;
        justify-content: end;
        height: 100%;
        @media (max-width: $tabletToMobile) {
            width: 100%;
        }
        .button {
            background: none;
            
        }
    }

}
</style>