<template>
    <div class="page content-container">
        <div v-if="!selectCar" class="select-car-block"></div>
        <div class="quiz-block">
            <progress id="progress" :value="progress" max="100"></progress>
            <keep-alive>
                <component :is="component" @earning="SetEarning" @budget="SetBudget" @progress="ChangeProgress" @employmentStatus="SetEmploymentStatus" @back="backComponent">
                </component>
                
            </keep-alive>
        </div>
    </div>
</template>
<script>
import budgetComponent from '@/components/405-page/budget-component.vue';
import employmentStatusComponent from '@/components/405-page/employment-status-component.vue';
import { shallowRef } from 'vue';
import earningIncomeComponentVue from '@/components/405-page/earning-income-component.vue';

export default {
    data() {
        return {
            component: shallowRef(budgetComponent),
            budget: 0,
            employedStatus: 0,
            earning: 0,
            progress: 10,
            history: [],
        }
    },
    props: {
        selectCar: {
            type: String,
            default: ''
        }
    },
    methods: {
        SetBudget(value) {
            this.budget = value;
            this.SetComponent(employmentStatusComponent);
        },
        SetEmploymentStatus(value) {
            this.employedStatus = value;
            this.SetComponent(earningIncomeComponentVue);
        },
        SetEarning(value){
            this.earning = value;
            this.SetComponent();
        },
        backComponent(){
            this.component = this.history.pop();
            
        },
        SetComponent(component){
            this.history.push(this.component);
            this.component = component;
        },
        ChangeProgress(value){
            this.progress = value;
        }
    },
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/default/default.scss";

.page.content-container {
    display: flex;
    justify-content: center;
    gap: 60px;

    .select-car-block {
        width: 420px;
        height: 395px;
        background: red;
    }

    .quiz-block {
        max-width: 454px;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 60px;

        #progress {
            width: 100%;
        }

    }
}
</style>