<template>
    <div class="our-credit-block">
        <div class="text-block">
            <h2 class="title">
                {{ this.title }}
            </h2>
            <p class="description">
                <slot></slot>
            </p>
            <base-button background>Request a Car</base-button>
        </div>
        <div class="img-block">
            <img class="img" :src=src alt="Car">
        </div>
    </div>
</template>
<script>
import baseButton from '../ui/base-button.vue'
export default {
  components: { baseButton },
    props:{
        title:{
            type: String,
            default: ''
        },
        aboutPage:{
            type: Boolean,
            default: false
        }
    },
    computed:{
        src(){
            if(!this.aboutPage) return require('@/assets/img/cars/Car2.png');
            else return require('@/assets/img/cars/Car4.png');
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/loan-page/our-credit-component.scss";
</style>