<template>
    <button class="base-button" :disabled="disabled" :class="buttonClass">
        <slot></slot>
    </button>
</template>
<script>
export default {
    data(){
        return{
            xss: '20px',
        }
    },
    name: 'base-button',
    props: {
        disabled:{
            type: Boolean,
            default: false
        },
        background: {
            type: Boolean,
            default: false
        },
        quiz:{
          type: Boolean,
          default: false,
        },
        paddingDesktop: {
            type: String,
            default: '16px 37px 15px 37px'
        },
        paddingTablet: {
            type: String,
            default: '16px 27px 15px 27px'
        },
        paddingMobile: {
            type: String,
            default: '10px 17px 10px 17px'
        },
    },
    computed: {
        buttonClass() {
            return {
                'background': this.background,
                'quiz': this.quiz,
            }
        },
    }
}

</script>
<style scoped lang="scss">
@import "@/assets/scss/default/default.scss";

.base-button {
  @include buttonText();
  display: block;
  border-radius: 2px;
  border: 1px solid $color2;
  cursor: pointer;
  padding: v-bind("paddingDesktop");
  @media (max-width: $deskToTablet) {
    padding: v-bind("paddingTablet");
  }
  @media (max-width: $tabletToMobile) {
    padding: v-bind("paddingMobile");
  }
  &:hover {
    border: 1px solid $color4;
  }
  &:disabled{
    border-color: $color2;
  }

  &.background {
    background-color: $color2;
    color: $background;

    &:hover {
      background-color: $color4;
    }
    &:disabled{
      background-color: $color2;
      opacity: 0.5;
    }
  }

  &:not(.background) {
    color: $color2;
    background: $background;

    &:hover {
      color: $color4;
    }
    &:disabled{
      color: $color2;
      opacity: 0.5;
    }
    &.quiz{
      border-color: #F1F1F1;
      color: #7C7A84;
      padding: 15px 15px;
      &.select{
        border-color: $color2;
        color: $color2;
      }
    }
  }
  
}

</style>