<template>
  <div class="blocks">
    <burger-button class="button" @click="openDrawer"></burger-button>
  </div>
  <teleport to="body">
    <div class="drawer-overlay" v-if="showDrawer" @click="closeDrawer"></div>
    <div class="drawer" :class="{ open: showDrawer }">
      <div class="drawer-header">

      </div>
      <div class="drawer-content">
        <keep-alive>
          <component :is="component" @close="closeDrawer" @changeComponent="changeComponent">
            <div v-if="menu" class="button-block">
              <burger-button class="button" isOpen @click="closeDrawer">
              </burger-button>
            </div>
          </component>
        </keep-alive>
      </div>
    </div>
  </teleport>
</template>

<script>
import menuComponent from './menu-component.vue';
import contactFormComponent from './contact-form-component.vue';
import { shallowRef } from 'vue';
import completeComponent from './complete-component.vue';
import burgerButton from '../ui/burger-button.vue';

export default {
  components: { burgerButton , menuComponent, contactFormComponent, completeComponent },
  props: {
    title: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      showDrawer: false,
      component: shallowRef(menuComponent),
      menu: true,
    };
  },
  methods: {
    openDrawer() {
      this.showDrawer = true;
      document.body.style.overflow = "hidden";
    },
    closeDrawer() {
      this.showDrawer = false;
      document.body.style.overflow = "";
    },
    handleEscapeKey(event) {
      if (event.keyCode === 27 && this.showDrawer == true) {
        this.closeDrawer();
      }
    },
    changeComponent(data) {
      this.component = data;
      if(data == 'menuComponent'){
        this.menu = true;
      }
      else this.menu = false;
    },
  },
  watch: {
    showDrawer(value) {
      if (value) {
        document.addEventListener("keyup", this.handleEscapeKey);

      } else {
        document.removeEventListener("keyup", this.handleEscapeKey);
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import '@/assets/scss/components/drawer/drawer-component.scss';
</style>