<template>
    <div class="budget-block">
        <h4 class="titleH4">What's your budget?</h4>
        <p class="description">Find vehicle options that fit your budget.</p>
        <div class="buttons-block">
            <base-button @click="selectValue(1)" :class="{ 'select': value == 1 }" quiz>Under $250/Month</base-button>
            <base-button @click="selectValue(2)" :class="{ 'select': value == 2 }" quiz>$250-374/Month</base-button>
            <base-button @click="selectValue(3)" :class="{ 'select': value == 3 }" quiz>$374-500/Month</base-button>
            <base-button @click="selectValue(4)" :class="{ 'select': value == 4 }" quiz>Over $500/Month</base-button>
            <base-button background :disabled="disabled" @click="sendValue">Сontinue</base-button>
        </div>

    </div>
</template>
<script>
import baseButton from '../ui/base-button.vue'

export default {
    components: { baseButton, },
    data() {
        return {
            value: 0,
            disabled: true,
            progress: 10,
        }
    },
    activated() {
        this.$emit('progress', this.progress);
    },
    methods: {
        selectValue(id) {
            this.disabled = false;
            this.value = id;
        },
        sendValue() {
            this.$emit('budget', this.value);
        }
    }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/components/405-page/405-page.scss";
</style>